import { useState, useRef, useCallback } from "react";
import styles from "./FormData.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faXmark,
	faPaperPlane,
	faVolumeHigh,
	faEye,
} from "@fortawesome/free-solid-svg-icons";
import { trigger } from "../../../customEvent";

function FormData(props) {
	const textAreaRef = useRef();
	const [value, setValue] = useState("");
	const [isSpeak, setIsSpeak] = useState(false);

	const ChangeStateOfSpakerBtn = () => {
		setIsSpeak((prev) => !prev);
	};
	const Speaker = useCallback(() => {
		// set state ben form (thong qua trung gian la ap)
		// ham nay dc chuyen tu formData sang day tong qua
		ChangeStateOfSpakerBtn();
		console.log("speaker r");
		let nodes = document.querySelector("#App-view-id").childNodes;
		function readAsyncSequence(Index, character) {
			const utterThis = new SpeechSynthesisUtterance(character);
			utterThis.lang = "zh-CN";
			speechSynthesis.speak(utterThis);
			// scroll đến element đang focus .
			nodes[Index].scrollIntoView({
				behavior: "smooth",
				block: "center",
			});
			nodes[Index].style.borderColor = "var(--LimeGreen)";
			return new Promise((resolve) => {
				utterThis.onend = function () {
					resolve(true);
				};
			});
		}
		const readAsync = () => {
			return new Promise(async (resolve) => {
				for (let i = 0; i < nodes.length; i++) {
					await readAsyncSequence(i, nodes[i].dataset.character).then(
						() => {
							nodes[i].style.borderColor = "transparent";
						}
					);
				}
				resolve(true);
			});
		};
		// ok!
		readAsync().then(() => {
			ChangeStateOfSpakerBtn();
		});
	}, []);
	return (
		<div id='form-wrapper' className={styles.wrapper} data-f>
			<form
				onReset={(e) => {
					e.preventDefault();
					textAreaRef.current.focus();
					setValue("");
				}}
				onSubmit={(e) => {
					e.preventDefault();
					// truyền dữ liệu đi
					trigger("toPinyin", value);
					trigger("emitData", value);
					setValue("");
					textAreaRef.current.focus();
				}}
			>
				<textarea
					ref={textAreaRef}
					className={"textarea"}
					spellCheck={false}
					value={value}
					onChange={(e) => {
						setValue(e.target.value);
					}}
					tabIndex={1}
				></textarea>
				<div className={styles["form-btn-group"]}>
					<button
						title='view pinyin'
						onClick={(e) => {
							e.preventDefault();
							props.ViewPinyinFn("view_pinyin");
						}}
					>
						<FontAwesomeIcon icon={faEye} />
					</button>
					<button
						type='submit'
						tabIndex={2}
						disabled={!(value.trim().length > 0) || isSpeak}
						className={`${value && styles.btnSubmit}`}
					>
						<FontAwesomeIcon icon={faPaperPlane} />
					</button>
					<button
						type='reset'
						tabIndex={3}
						disabled={!(value.trim().length > 0) || isSpeak}
						className={`${value && styles.btnReset}`}
					>
						<FontAwesomeIcon icon={faXmark} />
					</button>
					<button
						disabled={isSpeak}
						onClick={() => {
							Speaker();
						}}
					>
						<FontAwesomeIcon icon={faVolumeHigh} />
					</button>
				</div>
			</form>
		</div>
	);
}

export default FormData;
