// can luu lai trang thai cua thu muc de giu thu muc duoc mo
import React, {
	Suspense,
	useState,
	useEffect,
	useImperativeHandle,
	useRef,
	forwardRef,
	useCallback,
} from "react";

import { nanoid } from "nanoid";
import FormData from "./FormData";
import styles from "./main.module.scss";
import { on, off, trigger } from "./../../customEvent";
import TagScreen from "./Screens/Tag";
const HanziComponent = React.lazy(() => import("../HanziComponent"));
const NoteScreen = React.lazy(() => import("./Screens/Note"));
//const TagScreen = React.lazy(() => import("./Screens/Tag"));
const PinyinScreen = React.lazy(() => import("./Screens/Pinyin"));

// khi thay đổi tab,loading data => component này sẽ đc re-render mà ko làm các component khác re-render.
// thêm nút chỉnh sửa và lưu , lấy childNode của main-content lưu vào database
function MainContent(props, ref) {
	const noteScreenRef = useRef();
	const tagScreenRef = useRef();
	const [ArrayOfHanzi, setArrayOfHanzi] = useState([]);
	const [screen, setScreen] = useState("");
	const getFormData = useCallback(({ detail: _value }) => {
		if (screen === "view_pinyin") setScreen("viewr_char");
		if (!_value) {
			setArrayOfHanzi([]);
			return;
		}
		let ArrayOfChineseCharacter = [..._value]
			.join(" ")
			.trim()
			.split(/\s+/)
			.filter((char) => /[\u4e00-\u9fa5]/g.test(char))
			.map((char) => {
				return { Char_id: nanoid(5), char };
			});
		let $id = nanoid(5);
		setArrayOfHanzi(ArrayOfChineseCharacter);
		// luu gia tri goc nhan tu form data
		props.onHistoryChange({ _id: $id, _value });
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	useEffect(() => {
		// chỉ thêm nếu đang ở tab default
		// khi chuyển tab tab custom thì even này bị xóa (clean up)
		on("emitData", getFormData);
		return () => {
			off("emitData", getFormData);
		};
	}, []);
	// truyền ngược fn từ con lên cha (bằng cách này có thể gián tiếp gọi hàm trong component này từ một component khác[sidebar] va [formdata] thông qua component cha [app])
	useImperativeHandle(ref, () => {
		return {
			setArrayOfHanzi: function (v) {
				if (screen === "view_pinyin") {
					trigger("toPinyin", v);
					return;
				} else {
					let ArrayOfChineseCharacter = [...v]
						.join(" ")
						.trim()
						.split(/\s+/)
						.filter((char) => /[\u4e00-\u9fa5]/g.test(char))
						.map((char) => {
							return { Char_id: nanoid(5), char };
						});
					setArrayOfHanzi(ArrayOfChineseCharacter);
				}
			},
			setScreen,
			setTag: tagScreenRef.current?.setTag,
			getFormData,
			addTab: function (v) {
				noteScreenRef.current?.addNewTab(v);
				if (ArrayOfHanzi.length) {
					setArrayOfHanzi([]);
				}
			},
		};
	});

	return (
		<>
			<Suspense fallback={<div>Loading...</div>}>
				{screen === "viewr_char" && (
					<div className={styles["view_char_wrapper"]}>
						<FormData
							ViewPinyinFn={(v) => {
								setArrayOfHanzi([]);
								setScreen(v);
							}}
						/>

						<div id='App-view-id' className={styles["App-viewer"]}>
							{ArrayOfHanzi.map(({ Char_id, char }) => {
								return (
									<HanziComponent
										key={Char_id}
										character={char}
										options={props.hanziComponentOptions}
									/>
								);
							})}
						</div>
					</div>
				)}
				{screen === "note" && <NoteScreen ref={noteScreenRef} />}
				{screen === "view_pinyin" && (
					<PinyinScreen
						setScreen={setScreen}
						hanziComponentOptions={props.hanziComponentOptions}
					/>
				)}
			</Suspense>
			{screen === "tags" && <TagScreen ref={tagScreenRef} />}
		</>
	);
}

export default forwardRef(MainContent);
