import { useEffect, useRef, useState, memo, useMemo } from "react";
import HanziWriter from "hanzi-writer";
import pinyin from "pinyin";
import styles from "./HanziComponentStyle.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faCirclePlay,
	faCirclePause,
	faEarListen,
	faPencil,
} from "@fortawesome/free-solid-svg-icons";

import Loading from "../Loading";
import { useCallback } from "react";
function HanziComponent({ character, options }) {
	const [isLoading, setIsLoading] = useState(false);
	const [mode, setMode] = useState("animate");
	const [isAnimation, setIsAnimation] = useState(false);
	const [isPlaying, setIsplaying] = useState(false);
	const RenderElement = useRef();
	const [Elem, setElem] = useState(null);
	const r = useRef();
	const utterThis = new SpeechSynthesisUtterance(character);
	utterThis.lang = "zh-CN";

	const iconColor = isAnimation ? "var(--LimeGreen)" : "inherit";
	let title =
		mode === "quiz"
			? "draw mode!"
			: isAnimation
			? "click to pause"
			: "click to animate";

	const changeMode = () => {
		setMode((prevMode) => {
			let m = prevMode === "animate" ? "quiz" : "animate";
			return m;
		});
	};
	useEffect(() => {
		setIsLoading(true);
		let node = RenderElement.current;
		let el = HanziWriter.create(node, character, {
			...options,
			onLoadCharDataSuccess: function () {
				setIsLoading(false);
			},
			onLoadCharDataError: function (error) {
				loadingRef.current.innerHTML = `Error: ${error}`;
			},
		});
		setElem(el);
		return () => {
			node.innerHTML = null;
		};
	}, [character, options]);

	useEffect(() => {
		if (mode === "quiz") {
			Elem.quiz({
				onMistake: function () {
					r.current.innerText ??= "wrong!";
					let tID = setTimeout(() => {
						clearTimeout(tID);
						r.current.innerText = null;
					}, 800);
				},
				onCorrectStroke: function () {
					r.current.innerText ??= "correct!";
					let tID = setTimeout(() => {
						clearTimeout(tID);
						r.current.innerText = null;
					}, 800);
				},
				onComplete: function (summaryData) {
					let tID = setTimeout(() => {
						clearTimeout(tID);
						changeMode();
					}, 1000);
				},
			});
		}
	}, [mode, Elem]);

	const changeAnimationState = useCallback(() => {
		if (!isPlaying && mode !== "quiz") {
			setIsplaying(true);
			setIsAnimation(true);
			Elem.animateCharacter({
				onComplete: function () {
					setIsplaying(false);
					setIsAnimation(false);
				},
			});
		}

		if (isAnimation && isPlaying && mode !== "quiz") {
			Elem.pauseAnimation();
		} else {
			Elem.resumeAnimation();
		}
		if (mode !== "quiz") setIsAnimation(!isAnimation);
	}, [Elem, isAnimation, isPlaying, mode]);
	const loadingRef = useRef();
	const pinyin_char = useMemo(() => {
		return pinyin(character, {
			heteronym: true, // 启用多音字模式
		})[0].map((char, i) => {
			return (
				<span key={i} className='pinyin'>
					{char}
				</span>
			);
		});
	}, [character]);
	return (
		<div className={styles["wrapper"]} data-character={character}>
			{isLoading && <Loading ref={loadingRef} />}
			{mode === "quiz" && (
				<div className={styles["quiz-info"]}>
					<div ref={r}></div>
				</div>
			)}
			<div
				className={styles["Hanzi-wrapper"]}
				style={{ visibility: `${isLoading ? "hidden" : "visible"}` }}
				ref={RenderElement}
				onClick={changeAnimationState}
				title={title}
			></div>
			<div className={styles["pinyin-wrapper"]}>{pinyin_char}</div>
			<div className={styles["controler"]}>
				<button
					className={styles["btn"]}
					onClick={changeAnimationState}
					style={{
						color: iconColor,
					}}
					title={title}
				>
					{!isAnimation ? (
						<FontAwesomeIcon icon={faCirclePlay} />
					) : (
						<FontAwesomeIcon icon={faCirclePause} />
					)}
				</button>
				<div className={styles["right-section"]}>
					<button
						className={styles["btn"]}
						onClick={(e) => {
							e.target.style.color = "var(--LimeGreen)";
							speechSynthesis.speak(utterThis);
							utterThis.onend = function () {
								this.style.color = "inherit";
							}.bind(e.target);
						}}
					>
						<FontAwesomeIcon icon={faEarListen} />
					</button>
					<button
						className={styles["btn"]}
						onClick={changeMode}
						style={{
							color: `${
								mode === "animate"
									? "inherit"
									: "var(--LimeGreen)"
							}`,
						}}
					>
						<FontAwesomeIcon icon={faPencil} />
					</button>
				</div>
			</div>
		</div>
	);
}

export default memo(HanziComponent);
